@import url(https://fonts.googleapis.com/css?family=Open+Sans|Raleway:100i,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: white;
  background-image: url("https://image.freepik.com/free-vector/home-office-workspace-illustration_53876-2768.jpg");
  background-repeat: no-repeat;
  /* background-size: 60%; */
  background-position: bottom;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  font-family: 'Open Sans', sans-serif;
  color: #ff6f91;
}

h1, h2, h3, h4, h5 {
font-family: 'Raleway', sans-serif;
}
.footer{
  background-color: #cccce3;
  height: 108px;
  width: 100%;
  position: fixed;
  bottom: 0;
  box-shadow: 0px -2px 25px rgba(0, 0, 0, 0.5);  
  z-index: -100;
}

.switch-wrapper {
  position: relative;
}
 
.switch-wrapper > div {
  position: absolute;
  width: 100%;
}

